.disco {

    .shape-container .border,
    .bar .border,
    nav .ornament>div {
        background: linear-gradient(45deg, $red, $green, $blue, $purple, $yellow, $orange);
        background-size: 400% 400%;
        animation: discoAnimation 3s ease infinite;
    }

    nav .ornament svg {
        stroke: $yellow;
    }

    .octogon>.border,
    .octogon>.border>.content {
        border-radius: 5px;

        &::after,
        &::before,
        &::after,
        &::before {
            content: none;
        }
    }

    nav .ornament svg {
        stroke: linear-gradient(45deg, $red, $green, $blue, $purple, $yellow, $orange) !important;
        animation: none !important;
    }


    .center-line,
    .center-triangle .triangle .border,
    .me .hexagon .border {
        background: linear-gradient(45deg, $red, $green, $blue, $purple, $yellow, $orange) !important;
    }
}

@keyframes discoAnimation {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}